<template>
	<view>
		<view class="cu-bar bg-cyan search">
			<view class="search-form radius">
				<text class="cuIcon-search"></text>
				<input v-model.trim="keyword" :adjust-position="false" type="text" placeholder="输入关键词" confirm-type="search" @confirm="Search()"></input>
			</view>
			<view class="action" @click="Search()">
				<text class="cuIcon-search"></text>
				<text>搜索</text>
			</view>
		</view>
		<scroll-view scroll-x class="bg-white nav">
			<view class="flex text-center">
				<view class="cu-item flex-sub" :class="index==TabCur?'text-green cur':''"
					v-for="(item,index) in labelItem" :key="index" @tap="tabSelect" :data-id="index">
					{{item.name}}
				</view>
			</view>
		</scroll-view>
		<view  v-if="knowItem.length>0" class="cu-card article" :class="true?'no-card':''">
			
			<template v-for="(item,index) in knowItem">
			<view class="cu-item shadow" @click="ToDetail(item)">
				<view class="title"><view class="text-cut">{{item.name}}</view></view>
				<view class="content">
					<image :src="item.imageUrls|pictureFilter"
					 mode="aspectFill"></image>
					<view class="desc">
						<view class="text-content"> {{item.shortDescription}}</view>
						<view>
							<view class="cu-tag bg-red light sm round">{{item.creationTimeDateBeforeNow}}</view>
							<view class="cu-tag bg-green light sm round">热点:{{item.vistorCount}}</view>
						</view>
					</view>
				</view>
			</view>
		</template>
		</view>
		<view v-else class="solids-bottom padding-xs flex align-center bg-white">
			<view class="flex-sub text-center">
			
				<view style="min-height:100vh" class="padding">相关内容还没有哦~~~</view>
			</view>
		</view>
		<view style="height:120upx"></view>
			
			<footer-bar :currentIndex="1"></footer-bar>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				labelItem:[],
				TabCur:0,
				knowItem:[],
				keyword:""
			}
		},
		async onShow(){
			await this.KnowLedgeLabelList();
			await this.KnowLedgeList();
		},
		methods: {
			
			
			async KnowLedgeLabelList() {
				let url = "/WxApi/KnowLedgeWx/KnowLedgeLabelList";
				let {
					success,
					data: {
						items
					}
				} = await this.$http.PostAsync(url, {});
				if (success == true) {
					this.labelItem = [{name:"推荐",id:null},...items];
				}
			},	
			async KnowLedgeList() {
				let url = "/WxApi/KnowLedgeWx/KnowLedgeList";
				let knowLedgeLabelIds=this.labelItem[this.TabCur].id?[this.labelItem[this.TabCur].id]:[];
		        this.$comm.ShowLoading();
				let {
					success,
					data: {
						items
					}
				} = await this.$http.PostAsync(url, {knowLedgeLabelIds});
				if (success == true) {
					this.knowItem = items;
				}
				uni.hideLoading()
			},		
			async tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
				await this.KnowLedgeList();
				
				
			},
			async Search()
			{
				uni.navigateTo({
					url:`/pages/knowLedge/knowLedgeSearch?keyword=${this.keyword}`
				})
			},
			async ToDetail({id}){
				uni.navigateTo({
					url:`/pages/knowLedge/knowLedgeDetail?id=${id}`
				})
			}
		}
	}
</script>

